import 'whatwg-fetch';
import React from 'react';

import Scheduler from 'devextreme-react/scheduler';

import CustomStore from 'devextreme/data/custom_store';

function getData(_, requestOptions) {
  const PUBLIC_KEY = 'AIzaSyAycFzo3xFaaQk7YUe_Bz5BkhOjkfmu1E8',
    CALENDAR_ID = '5br7ca7lfdilgdb8vup790f1o0@group.calendar.google.com';
  const dataUrl = [ 'https://www.googleapis.com/calendar/v3/calendars/',
    CALENDAR_ID, '/events?key=', PUBLIC_KEY].join('');

  return fetch(dataUrl, requestOptions).then(
    (response) => response.json()
  ).then((data) => data.items);
}

const dataSource = new CustomStore({
  load: (options) => getData(options, { showDeleted: false })
});

const currentDate = new Date();
const views = ['day', 'week'];

class Sche extends React.Component {
  render() {
    return (
      <React.Fragment>
        <Scheduler
          dataSource={dataSource}
          views={views}
          defaultCurrentView="week"
          defaultCurrentDate={currentDate}
          height={500}
          startDayHour={0}
          endDayHour={24}
          editing={false}
          appointmentCollectorRender={false}
          showAllDayPanel={false}
          startDateExpr="start.dateTime"
          endDateExpr="end.dateTime"
          textExpr="summary"
          timeZone=""
          adaptivityEnabled="true" />
      </React.Fragment>

    );
  }
}

export default Sche;