import React, { useState, useEffect, useRef } from "react";
import { useHistory } from "react-router-dom";
import { baseURL, headers } from "../../services/service";
import './booking.css'
import axios from 'axios'
import Sche from "./Scheduler";



const Booking = () => {
    const [bigroom, setBigroom] = useState([])
    const [smallroom, setSmallroom] = useState([])
    const [studioroom, setStudioroom] = useState([])
    
    const [isBig, setIsBig] = useState(false)
    const [isSmall, setIsSmall] = useState(false)
    const [isStudio, setIsStudio] = useState(false)

    
    return (
        <div className="booking section">
            <h2>Rezerwacje</h2>     
            <div className="buttons">
                <div className="booking-buttons" onClick={() => {
                    setIsBig(false)
                    setIsSmall(true)
                    setIsStudio(false)
                }}>Sala mniejsza</div>
                <div className="booking-buttons" onClick={() => {
                    setIsBig(true)
                    setIsSmall(false)
                    setIsStudio(false)
                }}>Sala większa</div>
                <div className="booking-buttons" onClick={() => {
                    setIsBig(false) 
                    setIsSmall(false) 
                    setIsStudio(true)
                }}>Studio nagrań</div>
            </div>
            <div className="booking-main">
                <div className="schedule">
                <Sche/>
                </div>
                <div>
                <form className="find-item-container">
                    <div className="form-control">
                        <input 
                        type="text" 
                        placeholder="Godzina rozpoczęcia"  
                        />
                    </div>
                    <div className="form-control">
                        <input 
                        type="text" 
                        placeholder="Godzina zakończenia" 
                        />
                    </div>
                    <div className="form-control">
                        <input 
                        type="text" 
                        placeholder="Numer telefonu" 
                        />
                    </div>
                
                <input type="submit" value="Rezerwuj" className="btn"/>
                </form>
                </div>
            </div>
        </div>
    )
}

export default Booking
